    /* ////////////////////////////////accordion   in qa /////////////////////////////////// */ 


.accordion {
  background-color: #4d4d4d;
  color: #fdff6c;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  height:50px;
  border: lighter;
  border-color: #adf9fa;
  text-align: center;
  font-size:1rem;
  font-weight:bold;
  
  transition: 0.5s;
}

.active, .accordion:hover {
  background-color: #d7d863;
  color: #000;
}

.accordion:after {
  content: '\002B';
  color: #777;
  font-weight: bold;
  float: left;
  margin-left: 5px;
}

.active:after {
  content: "\2212";
}

.panel {
  padding: 0 18px;
  background-color: #fff;
  max-height: 0;
  /* overflow: hidden; */
  transition: max-height 0.2s ease-out;
}



